import React, { useState } from 'react'
import Button from './Button'
import topicValidator from '../validators/topicValidator'
import { CREATE_TOPIC } from '../queries/queries'
import { useMutation } from '@apollo/client'

const AddTopic = () => {

    const [name, setName] = useState('')
    const [error, setError] = useState('')
    const [success, setSuccess] = useState('')

    const [ createTopic ] = useMutation(CREATE_TOPIC)

    const addTopic = () => {
        const validation = topicValidator.validate(name)
        if (validation === 'success') {
            setError('')
            setSuccess('Aihe lisätty.')
            createTopic({  variables: { name } })
        } else {
            setSuccess('')
            setError(validation)
        }
    }

    return (
        <div className='Container'>
            {error !== '' && <p className='Error'>{error}</p>}
            {success !== '' && <p className='Success'>{success}</p>}
            <h1 className='Heading'>Lisää Aihe</h1>
            <input className='Aihe-input' value={name} type='text' onChange={(event) => setName(event.target.value)} />
            <Button type='Add-button' text='Lisää' handleClick={() => addTopic()} />
        </div>
    )
}

export default AddTopic
